@use "_include-media" as *;
// -----------------------------------------ПЕРЕМЕННЫЕ-----------------------------------------------
:root {

   --minWidth: 320px;
   --maxWidth: 1440px;
   
   // _____________________Размеры отступов_________________________________
   --space-unit: 1rem;
   
   --space-xxxxs: calc(0.125 * var(--space-unit));
   --space-xxxs: calc(0.25 * var(--space-unit));
   --space-xxs: calc(0.375 * var(--space-unit));
   --space-xs: calc(0.5 * var(--space-unit));
   --space-sm: calc(0.75 * var(--space-unit));
   --space-md: calc(1.25 * var(--space-unit));
   --space-lg: calc(2 * var(--space-unit));
   --space-xl: calc(3.25 * var(--space-unit));
   --space-xxl: calc(5.25 * var(--space-unit));
   --space-xxxl: calc(8.5 * var(--space-unit));
   --space-xxxxl: calc(13.75 * var(--space-unit));
   
   --component-padding: var(--space-md);
   
   // ____________________Цвета_________________________________________
   --clr-primary: rgba(75, 33, 255, 1);
   // --clr-primary: #5D96CF;
   --clr-mine-shaft: #272727;
   --clr-silver: #ADADAD;
   --clr-wild-sand: #F5F5F5;
   
   --clr-bg-main: #fff;
   --clr-bg-gray: #f5f5f57e;
   
   --clr-white: #fff;
   --clr-black: #000;
   
   --clr-success: rgb(38, 203, 124);
   --clr-error: rgb(255, 70, 66);
   --clr-warning: rgb(247, 125, 5);
   --clr-information: rgb(56, 132, 255);
   
   // _____________________Шрифт_________________________________
   --ff-main: 'Mulish', system-ui;
   --ff-header: 'Mulish', var(--ff-main);
   
   --ff-body: var(--ff-main);
   --ff-heading: var(--ff-main);
    
   --fw-thin: 100;
   --fw-ultra-light: 200;
   --fw-light: 300;
   --fw-regular: 400;
   --fw-medium: 500;
   --fw-semi-bold: 600;
   --fw-bold: 700;
   --fw-extra-bold: 800;
   --fw-heavy: 900;
   
   --fs-12: 0.75rem;
   --fs-14: 0.875rem;
   --fs-16: 1rem;
   --fs-18: 1.125rem;
   --fs-19: 1.1875rem;
   --fs-20: 1.25rem;
   --fs-22: 1.375rem;
   --fs-24: 1.5rem;
   --fs-32: 2rem;
   --fs-35: 2.1875rem;
   --fs-36: 2.25rem;
   --fs-40: 2.5rem;
   --fs-48: 3rem;
   --fs-50: 3.125rem;
   --fs-55: 3.4375rem;
   --fs-58: 3.625rem;
   --fs-65: 4.0625rem;
   --fs-72: 4.5rem;
   
   --fs-body: var(--fs-16);
   --fs-primary-heading: var(--fs-16);
   --fs-primary-btn: var(--fs-16);
   --fs-nav: var(--fs-16);

   --fs-logo: var(--fs-32);
   
   // _____________________Общие настройки_________________________________
   --sz-navigation: 3.75rem;
   // --sz-navigation-large: 6.5rem;
   --sz-main-padding: var(--space-lg);
   --sz-aside-width: max(9.375rem,35%);

   
   // --space-container-padding: var(--space-md);
   
   --bd-radius: .375rem;
}

